import { Component, OnInit } from '@angular/core';
import { Utils } from 'src/app/utils';

@Component({
  selector: 'app-custom-app-dev',
  templateUrl: './custom-app-dev.component.html',
  styleUrls: ['./custom-app-dev.component.css']
})
export class CustomAppDevComponent implements OnInit {

  isSmallDevice: number;
  constructor() {
    Utils.loadAllScripts();
  }

  ngOnInit() {
    this.isSmallDevice = screen.width;
  }

}
