import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductDevelopmentComponent } from './product-development/product-development.component';
import { CustomAppDevComponent } from './custom-app-dev/custom-app-dev.component';
import { WebServicesComponent } from './web-services/web-services.component';
import { AppMigrationComponent } from './app-migration/app-migration.component';
import { AppIntigrationComponent } from './app-intigration/app-intigration.component';
import { EngineeringServicesComponent } from './engineering-services/engineering-services.component';
import { QaComponent } from './qa/qa.component';
import { ServicesComponent } from './services.component';


const routes: Routes = [
  {
    path: '',
    component: ServicesComponent,
    children: [
      {
        path: 'ProductDevelopment',
        component: ProductDevelopmentComponent
      },
      {
        path: 'CustomApplicationDevelopment',
        component: CustomAppDevComponent
      },
      {
        path: 'webservices',
        component: WebServicesComponent
      },
      {
        path: 'app-migration',
        component: AppMigrationComponent
      },
      {
        path: 'app-intigration',
        component: AppIntigrationComponent
      },
      {
        path: 'engineering-services',
        component: EngineeringServicesComponent
      },
      {
        path: 'qa',
        component: QaComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ServicesRoutingModule { }
